import { IncomingMessage, Trigger } from '../trigger-data.types';
import { MessageTriggerParser } from './trigger-parser-base';
import { getRandomId } from '../../generalUtils';
import { MessageTrigger } from '../message-trigger.enum';

export class KinesisMessageParser extends MessageTriggerParser {
  _shouldHandle = (message: IncomingMessage): boolean => {
    return message?.Records?.[0]?.eventSource === 'aws:kinesis';
  };

  handle = (message: IncomingMessage, targetId: string | null): Trigger => {
    const arn = message.Records[0].eventSourceARN;
    const messageIds = (message.Records || [])
      .map((record) => record.kinesis.sequenceNumber)
      .filter((recordSequenceNumber) => recordSequenceNumber != null);
    return {
      id: getRandomId(),
      targetId: targetId,
      triggeredBy: MessageTrigger.Kinesis,
      fromMessageIds: messageIds,
      extra: { arn },
    };
  };
}
